import React from 'react';
import Link from "react-router-dom/Link";
const Footer = (props) => {
    return (
      <div>
            <footer className={props.classNameStyle}>
              <div className="container-fluid">
                <div className="row">
                      <div className="col-4">
                      <Link to = "/Legal" style={{color: "white"}}>Terms and Conditions / Legal</Link>
                      </div>
                      <div className="col-4">
                      <p>Contact Phone: (1)-760-415-7516</p>
                      </div>
                      <div className="col-4">
                      <p></p>
                    </div>
                </div>
              </div>
                
            </footer>
      </div>
    );
  }

export default Footer;