import React from 'react';
import Footer from './Footer';
import Link from "react-router-dom/Link";

export default class Contact extends React.Component {

  constructor(props) {
    super(props);
    this.state = {isFormSubmitted: false, isFormReSubmited: false};
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleButton = this.handleButton.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  handleSubmit(event) {
    if(this.state.isFormSubmitted===false)
    {
      this.setState(state => ({
      isFormSubmitted: true
       }));

       window.$.post("https://script.google.com/macros/s/AKfycbwkIfsbbMFwlgE4xn1VqwQk_aFkByYVcRQYOQRufA/exec", {firstName: this.state.firstName, lastName: this.state.lastName
       ,title: this.state.title, company: this.state.company, email: this.state.email, phone: this.state.phone, country: this.state.country, state: this.state.state, message: this.state.message}, (result)=>{
        console.log(result);    
         this.setState({status: result.status});
        });

    }
    else{
      this.setState(state => ({
        isFormReSubmited: true
         }));
    }

    event.preventDefault();
  }

  handleButton() {
    window.location = "https://www.linkedin.com/in/francesca-incardona-a8562b32/";
  }

  render() {
    return (
      <div>
      <div className="container">
        <br/>
        <h4 className="text-center"> Contact </h4>
        {this.state.isFormSubmitted === false?            
        <form className="gform pure-form pure-form-stacked text-center" method="POST" data-email="example@email.net" onSubmit={(e) => this.handleSubmit(e)}>
          <fieldset>
   
            
            <div className="pure-g form-elements" style={{textAlign:'center'}}>
                    <fieldset className="pure-u-1 pure-u-md-1-3">
                      <label htmlFor="firstName">First Name:</label>
                      <input className="pure-u-2-3" id="firstName" name="firstName" required placeholder="First Name" style={{display: 'inline'}} onChange={this.handleInputChange}/>
                    </fieldset>
                    
                    <fieldset className="pure-u-1 pure-u-md-1-3">
                      <label htmlFor="lastName">Last Name:</label>
                      <input className="pure-u-2-3" id="lastName" name="lastName" required placeholder="Last Name" style={{display: 'inline'}} onChange={this.handleInputChange}/>
                    </fieldset>

                    <fieldset className="pure-u-1 pure-u-md-1-3">
                      <label htmlFor="title">Title:</label>
                      <input className="pure-u-2-3" id="title" name="title" required placeholder="Title" style={{display: 'inline'}} onChange={this.handleInputChange}/>
                    </fieldset>

                    <fieldset className="pure-u-1 pure-u-md-1-3">
                      <label htmlFor="company">Company:</label>
                      <input className="pure-u-2-3" id="company" name="company" required placeholder="Company Name" style={{display: 'inline'}} onChange={this.handleInputChange}/>
                    </fieldset>

                    <fieldset className="pure-u-1 pure-u-md-1-3">
                      <label htmlFor="email"><em>Your</em> Email Address:</label>
                      <input className="pure-u-2-3" id="email" name="email" type="email" required placeholder="Your.name@email.com" style={{display: 'inline'}} onChange={this.handleInputChange}/>
                    </fieldset>

                    <fieldset className="pure-u-1 pure-u-md-1-3">
                      <label htmlFor="phone">Phone:</label>
                      <input className="pure-u-2-3" id="phone" name="phone" required placeholder="Phone number" style={{display: 'inline'}} onChange={this.handleInputChange}/>
                    </fieldset>

                    <fieldset className="pure-u-1 pure-u-md-1-3">
                      <label htmlFor="country">Country:</label>
                      <input className="pure-u-2-3" id="country" name="country" required placeholder="Country" style={{display: 'inline'}} onChange={this.handleInputChange}/>
                    </fieldset>

                    <fieldset className="pure-u-1 pure-u-md-1-3">
                      <label htmlFor="state">State:</label>
                      <input className="pure-u-2-3" id="state" name="state" required placeholder="State" style={{display: 'inline'}} onChange={this.handleInputChange}/>
                    </fieldset>

                    <fieldset className="pure-u-1 pure-u-md-1-3">
                      <label htmlFor="message">Message:</label>
                      <textarea id="message" name="message" cols="30" rows="10" required placeholder="Tell us how we can help you" style={{display: 'inline'}} onChange={this.handleInputChange}></textarea>
                    </fieldset>
            </div>
            
            <label className="sr-only">Keep this field blank</label>
            <input id="honeypot" type="text" name="honeypot" value="" onChange={this.handleInputChange}/>

            <label htmlFor="terms" className="pure-checkbox">
              <input id="terms" type="checkbox" required/> I've read the <Link to = "/Legal">terms and conditions</Link>
        </label>

            <button type="submit" className="pure-button pure-button-primary">Submit</button>
          </fieldset>
        </form>
        : <div className="thankyou_message text-center" style={{display: this.state.isFormSubmitted === true? 'block': 'none'}}>
                      <h2>{this.state.isFormReSubmited === true? <span>Your message has already been received!</span>  : <span><em>Thanks</em> for contacting us!
                        We will get back to you soon!</span>} </h2>
                    </div> }
       
        </div>
      <Footer classNameStyle="footer"/>
      </div>
    );
  }
}
