import React from 'react';
import Footer from './Footer';
const Home = () => {
    return (
      <div>
          <div style={{display:'flex' , justifyContent:'center' , alignItems:'center' , height:'40vh', padding: '10px',
          backgroundImage: `url(${"antibiotic-capsules-blue.jpg"})` , backgroundPosition: '50% 95%', backgroundSize: 'cover'}}>
          <h1 style={{ color:'white', margin:'0px', fontSize:'3.5rem'}} className="text-center">Preclinical consulting</h1>
          </div>

          <div className="container"  style={{padding:'50px'}}>
          <h4 className="center"> About </h4>
  <p>Blue Arrow Preclinical Consulting LLC with headquarters in Oceanside, CA, is a consortium of expert leaders in
    preclinical research and drug development providing guidance services to biotech, pharmaceutical, medical
    device companies and academia.<br></br>Our consultants are subject-matter experts mobilized here to supply our
    clients with efficient solutions from
    early discovery to IND enabling studies in the onco-immunology, inflammation and metabolic disease spaces.</p>
  <br></br>
  <h4>Our Mission and Values</h4>
  <p style={{marginBottom: "1rem"}}>Blue Arrow Preclinical Consulting aims to provide clients with expert scientific knowledge from early
    discovery to GLP toxicology studies in the areas of onco-immunology, inflammation, metabolic
    diseases and medical device testing.
We draw upon a large network of vetted independent pharmaceutical consultants, enabling us to lead you to
    success as thoughtful partners.
  We embody integrity, transparency, responsibility and passion. Our ultimate goal is to help you
    improve the quality of life for patients across the globe.</p>

          </div>
          <Footer classNameStyle="footerStatic"/>
      </div>
    );
  }

export default Home;