import React from 'react';
import Footer from './Footer';

const Bio = () => {
    return (
<div>
<div className="container">

  <br></br>

    <h3 className="center"> Management </h3>
    <br></br>
            <h3>Founder:</h3>
             <p>
             Dr. Francesca Incardona founded Blue Arrow Preclinical Consulting in November 2018 to address the
growing needs for scientists from the academia, and biotechnology and pharmaceutical companies to
access expert guidance in preclinical research and help accelerate the process of drug development.
             </p>
             <p>
             Dr. Incardona gathered a team of experts in their field of research such as in cancer, cancer
immunology, inflammation, metabolic diseases, dermatitis, wound healing, medical devices, to support
in vitro and in vivo study design guidance and monitoring for the evaluation of therapeutics drugs and
devices.
             </p>
             <p>
             Dr. Incardona has 18 years of experience in preclinical research and is most expert in cancer
immunology. She holds a Ph.D degree in Cell Biology from Paris VII, France. She has held positions with
increasing responsibilities from the University of Southern California, Cedars Sinai Medical center,
Biogen-Idec, The Novartis Foundation to a contract service company Bio-Quant/BTS Research where she
supported for more than 11 years a multitude of preclinical research projects in cancer and in other
diseases.
             </p>
             <p style={{marginBottom: "1rem"}}>
             Dr. Incardona, aims to provide with a team of trusted experts, a level of guidance and support of the
utmost quality.
             </p>
</div>
<Footer classNameStyle="footerStatic"/>
</div>
    );
  }

export default Bio;