import React from 'react';
import Link from "react-router-dom/Link";

const Navbar = () => {
    return (
      <nav className="navbar navbar-expand-lg navbar-light bg-light" style={{padding:'1em 5em 1em 5em'}}>
        <Link to = "/" className="navbar-brand"> {/*<img src="logo.png" width="30" height="30" alt=""/>*/}<span className="full-text">BAP | Blue Arrow Preclinical Consulting, LLC</span><span className="short-text">BAP</span></Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        {/*<div className="collapse navbar-collapse justify-content-center align-items-center w-100">
          <div className="navbar-nav">
            <Link to = "/" className="nav-item nav-link"> Center </Link>
          </div>
        </div>*/}

        <div className="collapse navbar-collapse justify-content-end" id="navbarNavAltMarkup">
          <div className="navbar-nav">
            <Link to = "/" className="nav-item nav-link">Home</Link>
            <Link to = "/About" className="nav-item nav-link">About</Link>
            <Link to = "/Services" className="nav-item nav-link">Services</Link>
            <Link to = "/Contact" className="nav-item nav-link" >Contact</Link>
          </div>
        </div>
      </nav>
    );
  }

export default Navbar;